import { Component } from '@angular/core';
import {  ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { pdfx } from '../../../../providers/pdf/pdfx';

@IonicPage()
@Component({
  selector: 'page-Unidadperiodo3a-list',
  templateUrl: 'unidadperiodo3a-list.html',
})
export class Unidadperiodo3aListPage {


  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';

  public idtipo                 : any;
  public idtipo2                 : any;
  public idnombre                 : any;
  public idnombre2                 : any;
  public idcodigo2                 : any;
  permisom: any=[];
  potros1: any=[];
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  pfiltro:boolean=false;
  jsonObject2 :any;

  ubica: any;
  fecha: any;
  idempresa:any;
  idafiliado:any='na';
  public hasFilter: boolean = false;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";

  constructor(  public Pdfx:  pdfx, public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })

    this.permisom = String(navParams.get("permiso")).split(",");
    this.potros1 = JSON.parse(JSON.stringify(navParams.get("potros1")))
    this.pingreso=(  this.permisom[0] == 'true');
    this.pconsulta=(  this.permisom[1] == 'true');
    this.peliminacion=(  this.permisom[2] == 'true');
    this.pcreacion=(  this.permisom[3] == 'true');
    this.pactualizacion=(  this.permisom[4] == 'true');
    this.pfiltro=(  this.permisom[5] == 'true');
    this.preporte=(  this.permisom[6] == 'true');

    var someJsonString = navParams.get("potros1");

    // jsonObject will contain a valid JavaScript object

   this.jsonObject2 = JSON.parse(someJsonString);
  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }


  public dapermisos2(data,opcion){
    var re='';

    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos.ingreso + ',' + data[i].permisos.consulta+ ',' + data[i].permisos.elimina+ ',' + data[i].permisos.creacion+ ',' + data[i].permisos.actualiza+ ',' + data[i].permisos.filtro+ ',' + data[i].permisos.reporte
        break;

      }

    }

    return re;
  }

  public dapermisos(data,opcion){
    var re:any[];

    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos
        break;

      }

    }


  //DD/MM/YYYY hh:mm A
  return re;

 }


 public dapermisos_visible(opcion){
  var re:boolean=false;

  for (var i = 0; i < this.jsonObject2.length; i++)
  {
    if(this.jsonObject2[i].modulo==opcion)
    {
        re=!this.jsonObject2[i].permisos.ingreso
      break;

    }

  }


//DD/MM/YYYY hh:mm A
return re;

}




  pideregistros() {
      let self = this;
      self.loading = true;

      this.regService.getReg(this.idtipo+'/'+ this.idtipo + '/todosver','unidadperiodo3as').then((data) => {

      this.items=data;
      this.items2=data;
      self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }
  ionViewDidLoad() {
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idnombre=this.navParams.data.record2.idnombre;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idnombre2=this.navParams.data.record2.idnombre2;
    this.idcodigo2=this.navParams.data.record2.idcodigo2;
    this.pideregistros();


  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  salonRecord(item : any, no) : void
  {

    this.navCtrl.push('unidadplan4aListPage', { record : item ,record2:{idtipo:this.idtipo,idnombre:this.idnombre,idtipo2:this.idtipo2,idnombre2:this.idnombre2,idtipo3:item._id,idnombre3:item.nombre,idcodigo2:this.idcodigo2}});

  }
/*
  this.idtipo=this.navParams.data.record.idtipounidad;
  this.idnombre=this.navParams.data.record.nombre;
  this.idtipo2=this.navParams.data.record._id;
  */

 reportehh(item : any, no) : void
 {
   this.regService.getReg2b( item.idtipounidad+'/'+ item.idtipounidad + '/'+item.nombre+'/1/horariocalusac','unidadplan3s').then((result) => {
   //  this.vpago=result;
   var  vdata:any=[];
   vdata=result;
   if(vdata.length==0)
   {
    this.msj.showError3('Información ','No existe periodo activo');

   }
   else
   {

    this.Pdfx.dahorarioingles(result,this.navParams.data.tnombre,item.nombre,this.navParams.data.tunidad);

   }

   }, (err) => {

   });


 }

  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'unidadperiodo3as').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();




  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('Unidadperiodo3aCreatePage',  { record : item ,record2:{idtipo:this.idtipo,idtipo2:this.idtipo2,idnombre:this.idnombre}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('Unidadperiodo3aCreatePage', { record : null ,record2:{idtipo:this.idtipo,idtipo2:this.idtipo2,idnombre:this.idnombre}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {

   }


   presentFilter() {

  }
}



